import { appSettings } from 'src/services/settings';
import { IWasmAdapter } from 'src/types/wasmAdapter';
import captureProcess from './captureProcess';
import { arrayToPoints, pointsToArray } from 'src/services/wasm/utils';
import CanvasHelper from 'src/services/wasm/canvasHelper';
import { getWasmWorker } from 'src/services/wasm-worker/wasm-worker-adapter';
import WASM_ACTION_TYPES from 'src/services/wasm-worker/WASM_ACTION_TYPES';
import isEdgesFound from 'src/utils/isEdgesFound';
import { getJpegFromBMPData } from 'src/utils/imageUtils';

const NUMBER_OF_VERTICES = 4;

/** high level api for Wasm, aware of user config
 * all interaction with wasm encapsulated here
 */
const wasmAdapter: IWasmAdapter = {
  loadWasmScripts: async (version = '') => {
    return await getWasmWorker().postMessage({
      type: WASM_ACTION_TYPES.LOAD_WASM,
      version,
      path: appSettings.userOptions.wasmFilesPath,
    });
  },

  checkLicense: async (licenseFilePath: string) => {
    await getWasmWorker().postMessage({
      type: WASM_ACTION_TYPES.CHECK_LICENSE,
      licenseFilePath,
    });
  },

  checkQualityAssessmentForOCR: async (jpegData, documentBoundary) => {
    if (!appSettings.userOptions.enableQualityAssessmentForOCR) {
      return null;
    }

    const jpegDataCopy = jpegData.slice(0); // wasm webworker nullify buffer after processing

    return getWasmWorker()
      .postMessage(
        {
          jpegData: jpegDataCopy, // because after worker ends with data, it clears
          documentBoundary,
          type: WASM_ACTION_TYPES.CHECK_QUALITY_OCR,
        },
        [jpegDataCopy.buffer]
      )
      .then((message) => message.result);
  },
  /**
   * @param jpegData
   * @param documentBoundary
   * @param imageQuality
   */
  recropImage: async (jpegData, documentBoundary, imageQuality = appSettings.userOptions.jpegQuality) => {
    const jpegDataCopy = jpegData.slice(0); // wasm webworker nullify buffer after processing

    return getWasmWorker()
      .postMessage(
        {
          jpegData: jpegDataCopy,
          documentBoundary: pointsToArray(documentBoundary),
          imageQuality,
          type: WASM_ACTION_TYPES.RECROP_IMAGE,
        },
        [jpegDataCopy.buffer]
      )
      .then((message) => message.result);
  },

  getResultFromSource: async (source, width, height, areaOfInterest) => {
    const sourceWidth = width || Number(source.width);
    const sourceHeight = height || Number(source.height);
    const areaOfInt = areaOfInterest || {
      x: 0,
      y: 0,
      width: sourceWidth,
      height: sourceHeight,
    };

    const canvasHelper = new CanvasHelper(areaOfInt.width, areaOfInt.height);

    canvasHelper.drawFrame(source, [-areaOfInt.x, -areaOfInt.y, sourceWidth, sourceHeight]);

    const sourceBMPData = canvasHelper.getBMPData();
    const sourceJpegData = canvasHelper.getJpegData();
    let croppedResult = {
      size: sourceJpegData.length,
      data: sourceJpegData,
      edges: new Array(NUMBER_OF_VERTICES).fill(0).map(() => ({ x: 0, y: 0 })),
    };

    if (appSettings.userOptions.enableAutoCrop) {
      croppedResult = await wasmAdapter.detectEdgesAndCrop(
        sourceBMPData.data,
        areaOfInt.width,
        areaOfInt.height,
        appSettings.userOptions.jpegQuality
      );
    }

    const ocrQuality = await wasmAdapter.checkQualityAssessmentForOCR(sourceJpegData, croppedResult.edges);

    return {
      originalImage: sourceJpegData,
      documentBoundary: croppedResult.edges,
      capturedImage: croppedResult.data,
      ocrQuality,
    };
  },

  detectEdgesAndCrop: async (
    bmpData: Uint8ClampedArray,
    imageWidth,
    imageHeight,
    imageQuality = appSettings.userOptions.jpegQuality
  ) => {
    const bmpDataCopy = bmpData.slice(0);

    return getWasmWorker()
      .postMessage(
        {
          bmpData: bmpDataCopy,
          imageWidth,
          imageHeight,
          imageQuality,
          type: WASM_ACTION_TYPES.DETECT_EDGES_AND_CROP,
        },
        [bmpDataCopy.buffer]
      )
      .then((message) => message.result);
  },

  captureProcess: {
    startCaptureProcess: ({ videoEl, areaOfInterest, onGetImage, onNextFrame, onStartHandleProcess }) => {
      if (!appSettings.userOptions.enableAutoCapture) {
        return;
      }

      captureProcess.start({
        videoEl,
        areaOfInterest,
        onNextFrame,
        onBestDocumentGet: async (originalImageBmp: ImageData, documentBoundary: Int32Array) => {
          onStartHandleProcess();

          const { width, height } = areaOfInterest;
          const originalImage = getJpegFromBMPData(width, height, originalImageBmp);

          // crop with quality crop
          const qualityResult = await wasmAdapter.detectEdgesAndCrop(originalImageBmp.data, width, height);

          if (isEdgesFound(pointsToArray(qualityResult.edges))) {
            onGetImage({
              originalImage,
              documentBoundary: qualityResult.edges,
              capturedImage: qualityResult.data,
            });
          } else {
            const capturedImage = await wasmAdapter.recropImage(originalImage, arrayToPoints(documentBoundary));

            onGetImage({
              originalImage,
              documentBoundary: arrayToPoints(documentBoundary),
              capturedImage,
            });
          }
        },
      });
    },

    stopCaptureProcess: async () => {
      await captureProcess.stop();
    },
  },
};

export default wasmAdapter;
