// split to chunks to come over max arguments stack error
const Uint8ToString = (u8a: Uint8ClampedArray) => {
  const CHUNK_SZ = 0x8000;
  const c = [];
  for (let i = 0; i < u8a.length; i += CHUNK_SZ) {
    c.push(String.fromCharCode.apply(null, u8a.subarray(i, i + CHUNK_SZ)));
  }
  return c.join('');
};

export const uint8ToBase64Jpeg = (arr: Uint8ClampedArray) => {
  return `data:image/jpeg;base64,` + btoa(Uint8ToString(arr));
};

export const base64JpegtoUint8 = (str: string) => {
  return new Uint8ClampedArray(
    atob(str.split(',')[1])
      .split('')
      .map((c) => {
        return c.charCodeAt(0);
      })
  );
};
