import { applyMiddleware, compose, createStore } from 'redux';
import reducer, { INITIAL_STATE } from 'src/app/reducers/reducer';
import onSaveMiddleware from 'src/app/middleware/onSave';
import { APP_MODES, ICaptureResult, IOnSave } from 'src/types/declaration';
import { appSettings } from 'src/services/settings';

export default (images: ICaptureResult[] = [], onSave: IOnSave) => {
  const composeEnhancers = (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

  let currentMode: APP_MODES = 'preview';

  if (images.length === 0 || appSettings.userOptions.startPage >= images.length) {
    currentMode = 'capture';
  }

  let startingPage = -1;

  if (images.length) {
    startingPage = Math.max(0, Math.min(appSettings.userOptions.startPage, images.length - 1));
  }

  const store = createStore(
    reducer,
    {
      ...INITIAL_STATE,
      images,
      currentMode,
      currentImageIndex: startingPage,
    },
    composeEnhancers(applyMiddleware(onSaveMiddleware(onSave)))
  );

  return store;
};
