import React from 'react';

import './app.scss';
import { APP_MODES, IAppStoreState } from 'src/types/declaration';
import ImagePreview from '../ImagePreview';
import CaptureView from '../CaptureView';
import CropView from '../CropView';
import { connect } from 'react-redux';

const Views: Record<APP_MODES, any> = {
  preview: ImagePreview,
  recapture: CaptureView,
  capture: CaptureView,
  crop: CropView,
};

interface IWorkingPanelProps {
  mode: APP_MODES;
}

class WorkingPanel extends React.Component<IWorkingPanelProps> {
  public render() {
    const CurrentView = Views[this.props.mode];

    return (
      <div className="ABBYY_Capture_Module__Container">
        <CurrentView />
      </div>
    );
  }
}

export default connect((state: IAppStoreState) => ({
  mode: state.currentMode,
}))(WorkingPanel);
