import React from 'react';

import './messagePopup.scss';

export type TMessage = {
    title: string;
    text: string;
};

const MessagePopup = ({messages, icon, buttonText, onButtonClick}:{messages: TMessage[], icon: string, buttonText: string, onButtonClick: () => void}) => {

  const msgs = messages.map((message, index) =>
    <div className="ABBYY_Capture_Module__Message" key={index}>
        <span className="ABBYY_Capture_Module__Message-Title">{message.title}</span>
        <span className="ABBYY_Capture_Module__Message-Text">{message.text}</span>
    </div>
  );
  const iconClass: string = 'ABBYY_Capture_Module__Message-Icon ABBYY_Capture_Module__Message-Icon--' + icon;

  return (
    <div className="ABBYY_Capture_Module__Message_Popup">
        <div className="ABBYY_Capture_Module__Message_Popup_Container">
            <div className={iconClass} />
            {msgs}
            <button onClick={onButtonClick}>{buttonText}</button>
        </div>
    </div>
    
  );
};

export default MessagePopup;