/** live capture from video stream */
import CanvasHelper from 'src/services/wasm/canvasHelper';
import { IWasmImageResult } from 'src/services/wasm-worker/wasmTypes';
import { TStatus } from 'src/services/wasm/CONSTANTS';
import { getScaledDimensions } from 'src/services/wasm/utils';
import isEdgesFound from 'src/utils/isEdgesFound';
import { IAreaOfInterest } from 'src/types/declaration';
import { getWasmWorker } from 'src/services/wasm-worker/wasm-worker-adapter';
import WASM_ACTION_TYPES from 'src/services/wasm-worker/WASM_ACTION_TYPES';

class CaptureProcess {
  public isRunning = false;
  private scale = 1;
  private scaledCanvas: CanvasHelper;
  private originalCanvas: CanvasHelper;
  private videoEl: HTMLVideoElement;

  private bestEdges: Int32Array = new Int32Array();
  private bestImageBmp: ImageData;

  private onBestDocumentGet: any;
  private onNextFrame: any;

  // video larger than area of interest so we draw image from video [-areaX,-areaY,
  // videoWidth, videoHeight] on canvas [areaWidth, areaHeight]
  private originalCanvasClip: number[];

  public start = async ({
    videoEl,
    areaOfInterest,
    onBestDocumentGet,
    onNextFrame,
  }: {
    videoEl: HTMLVideoElement;
    areaOfInterest: IAreaOfInterest;
    onBestDocumentGet: any;
    onNextFrame: any;
  }) => {
    if (this.isRunning) {
      return;
    }
    this.isRunning = true;

    const { width, height } = areaOfInterest;
    const { scale, scaledWidth, scaledHeight } = getScaledDimensions(width, height);

    this.scale = scale;

    this.originalCanvas = new CanvasHelper(width, height);
    this.scaledCanvas = new CanvasHelper(scaledWidth, scaledHeight);

    this.videoEl = videoEl;
    this.onBestDocumentGet = onBestDocumentGet;
    this.onNextFrame = onNextFrame;

    this.bestEdges = new Int32Array();

    this.originalCanvasClip = [-areaOfInterest.x, -areaOfInterest.y, videoEl.videoWidth, videoEl.videoHeight];

    await getWasmWorker().postMessage({
      width: scaledWidth,
      height: scaledHeight,
      type: WASM_ACTION_TYPES.START_IMAGE_DETECTION,
    });

    this.captureNextFrame();
  };

  public async stop() {
    if (this.isRunning) {
      this.isRunning = false;
      await getWasmWorker().postMessage({
        type: WASM_ACTION_TYPES.STOP_IMAGE_DETECTION,
      });
    }
  }

  // status must be ready & document vertices must be found
  private continueCapture(result: IWasmImageResult) {
    return result.status !== TStatus.ResultRead || !isEdgesFound(this.bestEdges);
  }

  public captureNextFrame = async () => {
    if (!this.isRunning) {
      return;
    }

    this.originalCanvas.drawFrame(this.videoEl, this.originalCanvasClip);
    this.scaledCanvas.drawFrame(this.originalCanvas.canvas);

    const bmpData = this.scaledCanvas.getBMPData();

    const result = await getWasmWorker()
      .postMessage(
        {
          bmpData: bmpData.data,
          type: WASM_ACTION_TYPES.CAPTURE_NEXT_FRAME,
        },
        [bmpData.data.buffer]
      )
      .then((message) => message.result);

    if (result.isNewBest && isEdgesFound(result.vertices)) {
      this.bestEdges = result.vertices;
      this.bestImageBmp = this.originalCanvas.getBMPData();
    }

    if (this.continueCapture(result)) {
      this.onNextFrame(result.vertices);
      requestAnimationFrame(this.captureNextFrame);
    } else {
      this.stop();
      await this.returnResult();
    }
  };

  private returnResult = async () => {
    this.onBestDocumentGet(this.bestImageBmp, this.bestEdges.map((x) => x / this.scale));
  };
}

export default new CaptureProcess();
