// external API options
import { IWebCaptureOptions } from 'src/types';
import { setLocalizedStrings } from 'src/services/localization/strings';

export interface ITSettings {
  capture: {
    videoSource: any;
    minFramesCount: number;
    minCaptureTime: number;
    defCaptureHeight: number;
  };
  recognition: {
    defaultServerUrl: string;
    ocrQualityBound: number;
  };
  ui: {
    startPictureShowDelay: number;
    timeToPulseFrameShow: number;
    edgesWidth: number;
    edgesColor: string;
  };
  cameraConstraints: Partial<MediaTrackConstraints>;
  userOptions: IWebCaptureOptions;
}

export const appSettings: ITSettings = {
  capture: {
    videoSource: null, // external video stream to recognize from
    minFramesCount: 6, // min frames before wasm can report for good frame
    defCaptureHeight: 1024, // default height of reduced frame to live capture
    minCaptureTime: 1500, // min time to capture, ms
  },
  recognition: {
    // server with recognition functionality
    defaultServerUrl: 'https://test-webcapture.abbyy.com/api/RtrSimple',
    ocrQualityBound: 0.6,
  },
  ui: {
    startPictureShowDelay: 2000, // time to first image show
    timeToPulseFrameShow: 2000, // time to first show pulse frame
    edgesWidth: 5, // visual edge width
    edgesColor: '#ffdc48', // visual edge color
  },
  cameraConstraints: {
    // ideal camera resolution
    width: { ideal: 1920 },
    height: { ideal: 1080 },
    facingMode: 'environment',
  },
  /* configurable options */
  userOptions: {
    jpegQuality: 80,
    startPage: 0,
    maxImagesCount: Number.MAX_SAFE_INTEGER,
    enableCamera: true,
    enableAutoCapture: true,
    enableAutoCrop: true,
    enableQualityAssessmentForOCR: false,
    licenseFilePath: '/js/MWC.ABBYY.License',
    wasmFilesPath: 'libs/js',
  },
};

export const setAppOptions = (newOptions: Partial<IWebCaptureOptions>) => {
  const { localizedStrings, ...options } = newOptions;

  if (localizedStrings) {
    setLocalizedStrings(localizedStrings);
  }

  appSettings.userOptions = {
    ...appSettings.userOptions,
    ...options,
  };
};
