import React, { ChangeEvent, createRef, RefObject } from 'react';

import './captureBottomPanel.scss';

import { ICaptureResult } from 'src/types/declaration';
import wasmAdapter from 'src/services/wasm/wasmAdapter';

interface ICaptureBottomPanelProps {
  stopCapture: () => void;
  captureCurrent: () => void;
  onGetImage: (result: ICaptureResult) => void;
  beforeGetImage: () => Promise<void>;
  onFileSelectButtonClick: () => void;
  disableCapture: boolean;
  showFileLoadButton: boolean;
  onError: () => void;
  galleryInputRef: RefObject<HTMLInputElement>;
}

interface ICaptureBottomPanelState {
  manualCaptureDisabled: boolean;
}

class CaptureBottomPanel extends React.Component<ICaptureBottomPanelProps, ICaptureBottomPanelState> {
  private buttonRef = createRef<HTMLDivElement>();
  private fileButtonRef = createRef<HTMLDivElement>();

  public state = {
    manualCaptureDisabled: true,
  };

  public componentDidMount(): void {
    this.setState({
      manualCaptureDisabled: false,
    });
  }

  private _handleFile = (file: File) => {
    return new Promise((resolve) => {
      const reader: FileReader = new FileReader();

      reader.onload = () => {
        const dataURL = reader.result as string;

        const img: HTMLImageElement = new Image();

        // draw on canvas and get result
        img.onload = async () => {
          try {
            const result = await wasmAdapter.getResultFromSource(img);

            resolve(result);
          } catch (e) {
            this.props.onError();
          }
        };

        img.onerror = () => {
          this.props.onError();
        };

        img.src = dataURL;
      };

      reader.readAsDataURL(file);
    });
  };

  private _onFileSelected = async (ev: ChangeEvent<HTMLInputElement>) => {
    ev.persist();

    await this.props.beforeGetImage();

    const file = ev.target.files[0];
    const result: ICaptureResult = (await this._handleFile(file)) as ICaptureResult;

    this.props.onGetImage(result);
  };

  private _onManualCaptureClick = () => {
    this.props.beforeGetImage().then(() => {
      this.setState({ manualCaptureDisabled: true });
      this.props.captureCurrent();
    });
  };

  private _onGetFileClick = () => {
    this.props.galleryInputRef.current.click();
    this.props.onFileSelectButtonClick();
  };

  public render() {
    const captureButtonClass: string =
      'ABBYY_Capture_Module_CaptureView__Capture_Button' +
      (this.state.manualCaptureDisabled || this.props.disableCapture
        ? ' ABBYY_Capture_Module_CaptureView__Capture_Button--disabled'
        : '');

    return (
      <div className="ABBYY_Capture_Module_CaptureView_ButtonContainer">
        <div
          className="ABBYY_Capture_Module_CaptureView_Button ABBYY_Capture_Module_CaptureView_Button__Close_Button"
          onClick={this.props.stopCapture}
        />
        <div ref={this.buttonRef} className={captureButtonClass} onClick={this._onManualCaptureClick} />
        {this.props.showFileLoadButton && (
          <input
            type="file"
            accept="image/jpeg,image/png,image/gif"
            onChange={this._onFileSelected}
            ref={this.props.galleryInputRef}
          />
        )}
        {this.props.showFileLoadButton && (
          <div
            ref={this.fileButtonRef}
            className="ABBYY_Capture_Module_CaptureView_Button ABBYY_Capture_Module_CaptureView_Button__Get_File_Button"
            onClick={this._onGetFileClick}
          />
        )}
      </div>
    );
  }
}

export default CaptureBottomPanel;
