import React, { createRef } from 'react';
import { IPoint } from 'src/types';
import { get, throttle } from 'lodash';

interface ICropLayerProps {
  cropPoints: IPoint[];
  updatePoint: (i: number, updatedPoint: IPoint) => void;
  svgExtraSpace: number;
  scale: number;
}

interface ICropLayerState {
  maxX: number;
  maxY: number;
}

interface IDragEl {
  index: number;
  node: SVGGeometryElement;
  startEventPos: { x: number; y: number };
  currentPos: { x: number; y: number };
}

type UserEvent = React.TouchEvent | React.MouseEvent;

const BORDER_RADIUS = 1;
const CIRCLE_RADIUS = 9;
const SELECTED_CIRCLE_RADIUS = 15;

const EVENTS =
  'ontouchstart' in window
    ? {
        move: 'touchmove',
        end: 'touchend',
      }
    : {
        move: 'mousemove',
        end: 'mouseup',
      };

class CropLayer extends React.Component<ICropLayerProps, ICropLayerState> {
  private svgRef = createRef<SVGSVGElement>();
  private lineRefs = [
    createRef<SVGLineElement>(),
    createRef<SVGLineElement>(),
    createRef<SVGLineElement>(),
    createRef<SVGLineElement>(),
  ];
  private pointRefs = [
    createRef<SVGGeometryElement>(),
    createRef<SVGGeometryElement>(),
    createRef<SVGGeometryElement>(),
    createRef<SVGGeometryElement>(),
  ];
  private dragEl: IDragEl = null;

  private _getCoordsFromEvent = (ev: Event) => {
    if (ev instanceof MouseEvent) {
      return {
        x: ev.clientX,
        y: ev.clientY,
      };
    } else if (ev instanceof TouchEvent) {
      return {
        x: ev.touches[0].clientX,
        y: ev.touches[0].clientY,
      };
    }
  };

  private _dragStart = (ev: UserEvent) => {
    const startEventPos = this._getCoordsFromEvent(ev.nativeEvent);

    // go through all points and find nearest distance
    const findNearestPointIndex = () => {
      const svgRect = this.svgRef.current.getBoundingClientRect();
      const relativeStartPos = {
        x: startEventPos.x - svgRect.left,
        y: startEventPos.y - svgRect.top,
      };

      return this.props.cropPoints.reduce(
        ([nearestIndex, maxDistance], point, i) => {
          const distanceToPoint =
            (point.x - relativeStartPos.x) * (point.x - relativeStartPos.x) +
            (point.y - relativeStartPos.y) * (point.y - relativeStartPos.y);

          if (distanceToPoint < maxDistance) {
            return [i, distanceToPoint];
          }

          return [nearestIndex, maxDistance];
        },
        [-1, Number.MAX_SAFE_INTEGER]
      )[0];
    };

    const index = findNearestPointIndex();

    this.dragEl = {
      index,
      node: this.pointRefs[index].current,
      startEventPos,
      currentPos: {
        ...this.props.cropPoints[index],
      },
    };
    // redraw selected point
    this.forceUpdate();
  };

  private movePoint = throttle((ev: Event) => {
    if (!this.dragEl) {
      return;
    }

    this.dragEl.currentPos = this._getElementPos(this.dragEl.index, ev);
    const { x, y } = this.dragEl.currentPos;
    const nextLineIndex = this.dragEl.index === this.props.cropPoints.length - 1 ? 0 : this.dragEl.index + 1;

    // update point
    this.dragEl.node.setAttribute('transform', this._getTranslate({ x, y }));

    // update lines
    this.lineRefs[this.dragEl.index].current.setAttribute('x1', String(x));
    this.lineRefs[this.dragEl.index].current.setAttribute('y1', String(y));
    this.lineRefs[nextLineIndex].current.setAttribute('x2', String(x));
    this.lineRefs[nextLineIndex].current.setAttribute('y2', String(y));
    // tslint:disable-next-line:no-magic-numbers
  }, 50);

  private _dragMove = (ev: Event) => {
    if (this.dragEl) {
      ev.preventDefault();
      ev.stopPropagation();

      this.movePoint(ev);
    }
  };

  private _dragEnd = () => {
    if (this.dragEl) {
      this.props.updatePoint(this.dragEl.index, this.dragEl.currentPos);
      this.dragEl = null;
      this.forceUpdate();
    }
  };

  private _getElementPos = (i: number, ev: Event) => {
    const { x, y } = this._getCoordsFromEvent(ev);

    return {
      x: Math.max(0, Math.min(this.props.cropPoints[i].x + x - this.dragEl.startEventPos.x, this.state.maxX)),
      y: Math.max(0, Math.min(this.props.cropPoints[i].y + y - this.dragEl.startEventPos.y, this.state.maxY)),
    };
  };

  private _getTranslate = ({ x, y }: { x: number; y: number }) => {
    return 'translate(' + x + ', ' + y + ')';
  };

  private _renderPoints = () => {
    const dragIndex = get(this.dragEl, 'index', -1);

    return (
      <>
        {this.props.cropPoints.map((cropPoint, i) => {
          return (
            <g key={i} transform={this._getTranslate(cropPoint)} data-id={i} ref={this.pointRefs[i]}>
              <circle r={dragIndex === i ? 2 : CIRCLE_RADIUS} fill="white" />
              <circle
                r={dragIndex === i ? SELECTED_CIRCLE_RADIUS : CIRCLE_RADIUS}
                fill="transparent"
                stroke="#ffdc48"
                strokeWidth={BORDER_RADIUS}
              />
              <circle fill="transparent" r={this.props.svgExtraSpace} style={{ cursor: 'pointer' }} />
            </g>
          );
        })}
      </>
    );
  };

  private _renderLines = () => {
    return (
      <>
        {this.props.cropPoints.map((p1, i) => {
          const prevPointIndex = i === 0 ? this.props.cropPoints.length - 1 : i - 1;
          const p2 = this.props.cropPoints[prevPointIndex];

          return (
            <line
              ref={this.lineRefs[i]}
              key={i}
              x1={p1.x}
              x2={p2.x}
              y1={p1.y}
              y2={p2.y}
              stroke="#ffdc48"
              strokeWidth={BORDER_RADIUS}
            />
          );
        })}
      </>
    );
  };

  private _onSVGRender = () => {
    if (this.svgRef.current.getBoundingClientRect().width && this.svgRef.current.getBBox().width) {
      const { width, height } = this.svgRef.current.getBoundingClientRect();

      this.setState({
        maxX: Number(width - 2 * this.props.svgExtraSpace - 1),
        maxY: Number(height - 2 * this.props.svgExtraSpace - 1),
      });
      this.svgRef.current.setAttribute(
        'viewBox',
        `${-this.props.svgExtraSpace} ${-this.props.svgExtraSpace} ${width} ${height}`
      );
    } else {
      requestAnimationFrame(this._onSVGRender);
    }
  };

  public componentDidMount(): void {
    this._onSVGRender();

    document.addEventListener(EVENTS.move, this._dragMove, { passive: false });
    document.addEventListener(EVENTS.end, this._dragEnd, true);
  }

  public componentWillUnmount(): void {
    document.removeEventListener(EVENTS.move, this._dragMove);
    document.removeEventListener(EVENTS.end, this._dragEnd);
  }

  public componentDidUpdate(
    prevProps: Readonly<ICropLayerProps>,
    prevState: Readonly<ICropLayerState>,
    snapshot?: any
  ): void {
    if (this.props.scale !== prevProps.scale) {
      this._onSVGRender();
    }
  }

  public render() {
    return (
      <svg
        className="ABBYY_Capture_Module__CropView_CropLayer"
        ref={this.svgRef}
        onMouseDown={this._dragStart}
        onTouchStart={this._dragStart}
      >
        {this._renderLines()}
        {this._renderPoints()}
      </svg>
    );
  }
}

export default CropLayer;
