import actionTypes from './actionTypes';
import { APP_MODES, ICaptureResult } from 'src/types/declaration';

export const changeMode = (mode: APP_MODES) => ({
  payload: {
    mode,
  },
  type: actionTypes.CHANGE_MODE,
});

export const addImage = (image: ICaptureResult) => ({
  payload: {
    image,
  },
  type: actionTypes.ADD_IMAGE,
});

export const replaceImage = (image: ICaptureResult) => ({
  payload: {
    image,
  },
  type: actionTypes.REPLACE_IMAGE,
});

export const deleteCurrentImage = () => ({
  type: actionTypes.DELETE_CURRENT_IMAGE,
});

export const goToNextImage = () => ({
  type: actionTypes.GOTO_NEXT_IMAGE,
});

export const goToPrevImage = () => ({
  type: actionTypes.GOTO_PREV_IMAGE,
});

export const saveData = () => ({
  type: actionTypes.SAVE_DATA,
});
