import { appSettings } from 'src/services/settings';
import { base64JpegtoUint8 } from 'src/utils/base64Conversion';

export const getBMPData = (width: number, height: number, image: HTMLImageElement) => {
  const canvas: HTMLCanvasElement = document.createElement('canvas');
  const ctx = canvas.getContext('2d');

  canvas.width = width;
  canvas.height = height;

  if (width === image.naturalWidth && height === image.naturalHeight) {
    ctx.drawImage(image, 0, 0);
  } else {
    // safari memory leak in drawImage with many arguments
    ctx.drawImage(image, 0, 0, image.naturalWidth, image.naturalHeight, 0, 0, width, height);
  }

  return ctx.getImageData(0, 0, canvas.width, canvas.height).data;
};

export const getJpegFromBMPData = (width: number, height: number, data: ImageData) => {
  const canvas: HTMLCanvasElement = document.createElement('canvas');
  const ctx = canvas.getContext('2d');

  canvas.width = width;
  canvas.height = height;
  ctx.putImageData(data, 0, 0);

  return base64JpegtoUint8(canvas.toDataURL('image/jpeg', appSettings.userOptions.jpegQuality / 100));
};
