import React, { createRef } from 'react';
import { connect } from 'react-redux';

import styles from './cropView.scss';
import { IPoint, TQualityAssessmentResult } from 'src/types';
import { IAppStoreState, ICaptureResult, TJpegResult } from 'src/types/declaration';
import CropLayer from 'src/components/CropView/CropLayer';
import { changeMode, replaceImage } from 'src/app/actions/actionCreators';
import { ActionCreator, AnyAction } from 'redux';
import wasmAdapter from 'src/services/wasm/wasmAdapter';
import TopPanel from 'src/components/TopPanel';
import NavBar from 'src/components/NavBar';
import ProcessPage from 'src/components/ProcessPage';
import { STRINGS } from 'src/services/localization/strings';

const svgExtraSpace = parseInt(styles.svgExtraSpace, 10);

interface ICropViewStateProps {
  captureResult: ICaptureResult;
  imageBlob: string;
}

interface ICropViewDispachProps {
  switchToPreview: ActionCreator<AnyAction>;
  replaceImage: ActionCreator<AnyAction>;
}

interface ICropViewState {
  cropPoints: IPoint[];
  scale: number; // scale of image
  showProcessPage: boolean;
}

class CropView extends React.Component<ICropViewStateProps & ICropViewDispachProps, ICropViewState> {
  private imgRef = createRef<HTMLImageElement>();
  private imageContainerRef = createRef<HTMLDivElement>();
  private cropWorkingPaneRef = createRef<HTMLDivElement>();
  private detectScaleTimeout: number;

  constructor(props: ICropViewStateProps & ICropViewDispachProps) {
    super(props);

    this.state = {
      scale: 1,
      cropPoints: [], // state here reflects svg for saving functionality
      showProcessPage: false,
    };
  }

  private _updatePoint = (index: number, newPos: IPoint) => {
    this.state.cropPoints[index] = newPos;

    this.setState({
      cropPoints: [...this.state.cropPoints],
    });
  };

  public componentDidMount(): void {
    this._setMaxHeight();
  }

  public componentDidUpdate(
    prevProps: Readonly<ICropViewStateProps & ICropViewDispachProps>,
    prevState: Readonly<ICropViewState>,
    snapshot?: any
  ): void {
    if (prevProps.imageBlob !== this.props.imageBlob) {
      URL.revokeObjectURL(prevProps.imageBlob);
      if (this.detectScaleTimeout) {
        clearTimeout(this.detectScaleTimeout);
      }

      this.setState({ cropPoints: [] });
      // wait for image dimensions to be updated by browser
      this.detectScaleTimeout = window.setTimeout(this._detectScale, 100);
    }
  }

  private _setMaxHeight = () => {
    const cropWorkingPaneHeight = this.cropWorkingPaneRef.current.offsetHeight;

    if (cropWorkingPaneHeight) {
      this.imageContainerRef.current.style.maxHeight = cropWorkingPaneHeight - 2 * svgExtraSpace + 'px';
      this.imageContainerRef.current.style.visibility = 'visible';
      this._detectScale();
    } else {
      requestAnimationFrame(this._setMaxHeight);
    }
  };

  private _detectScale = () => {
    if (this.imgRef.current.width) {
      const image = this.imgRef.current;
      let cropPoints = this.props.captureResult.documentBoundary;
      const scale = image.width / image.naturalWidth;

      // if all crop points the same -> crop points 0,0,width,height
      if (cropPoints[0].x === cropPoints[1].x && cropPoints[0].y === cropPoints[1].y) {
        cropPoints = [
          { x: 0, y: image.naturalHeight - 1 },
          { x: 0, y: 0 },
          { x: image.naturalWidth - 1, y: 0 },
          { x: image.naturalWidth - 1, y: image.naturalHeight - 1 },
        ];
      }

      this.setState({
        scale,
        cropPoints: cropPoints.map((p) => ({
          x: p.x * scale,
          y: p.y * scale,
        })),
      });
    } else {
      requestAnimationFrame(this._detectScale);
    }
  };

  private _reCrop = () => {
    this.setState({
      showProcessPage: true,
    });

    const img = new Image();

    img.onload = async () => {
      const documentBoundary: IPoint[] = this.state.cropPoints.map((p) => ({
        x: p.x / this.state.scale,
        y: p.y / this.state.scale,
      }));

      try {
        const croppedJpegData = await wasmAdapter.recropImage(this.props.captureResult.originalImage, documentBoundary);

        this._processCropResult({
          size: croppedJpegData.length,
          edges: documentBoundary,
          data: croppedJpegData,
        });
      } catch (e) {
        this.setState({
          showProcessPage: false,
        });
        this.props.switchToPreview();
      }
    };
    img.src = this.props.imageBlob;
  };

  /*private _autoCrop = () => {
    this.setState({
      showProcessPage: true,
    });

    const img = new Image();

    img.onload = () => {
      const imageCanvas = new CanvasHelper(img.naturalWidth, img.naturalHeight);

      imageCanvas.drawFrame(img);

      this._processCropResult(
        wasmAdapter.detectEdgesAndCrop(
          imageCanvas.getBMPData(),
          img.naturalWidth,
          img.naturalHeight,
          appSettings.userOptions.jpegQuality
        )
      );
    };
    img.src = this.props.imageBlob;
  };*/

  private _processCropResult = async (croppedResult: TJpegResult) => {
    const ocrQuality: TQualityAssessmentResult = await wasmAdapter.checkQualityAssessmentForOCR(
      this.props.captureResult.originalImage,
      croppedResult.edges
    );

    this.setState({
      showProcessPage: false,
    });

    this.props.replaceImage({
      ...this.props.captureResult,
      capturedImage: croppedResult.data,
      documentBoundary: croppedResult.edges,
      ocrQuality,
    });
  };

  public render() {
    return (
      <div className="ABBYY_Capture_Module__CropView">
        <TopPanel>
          <span className="button" onClick={this.props.switchToPreview}>
            {STRINGS.cancel}
          </span>
          <span>{STRINGS.crop}</span>
          <span className="button" onClick={this._reCrop}>
            {STRINGS.apply}
          </span>
        </TopPanel>
        <div className="ABBYY_Capture_Module__CropView_WorkingPane" ref={this.cropWorkingPaneRef}>
          <div ref={this.imageContainerRef}>
            <img ref={this.imgRef} src={this.props.imageBlob} />
            <CropLayer
              scale={this.state.scale}
              cropPoints={this.state.cropPoints}
              updatePoint={this._updatePoint}
              svgExtraSpace={svgExtraSpace}
            />
          </div>
        </div>
        <div className="ABBYY_Capture_Module__CropView_BottomPane">
          {/*<div className="ABBYY_Capture_Module__CropView_AutocropContainer">
            <span className="button" onClick={this._autoCrop}>
              {STRINGS.autocrop}
            </span>
          </div>*/}
          <NavBar />
        </div>
        {this.state.showProcessPage && <ProcessPage message={STRINGS.image_processing} />}
      </div>
    );
  }
}

export default connect(
  (state: IAppStoreState) => ({
    captureResult: state.images[state.currentImageIndex],
    imageBlob: window.URL.createObjectURL(
      new Blob([state.images[state.currentImageIndex].originalImage], { type: 'image/jpeg' })
    ),
  }),
  {
    switchToPreview: () => changeMode('preview'),
    replaceImage,
  }
)(CropView);
