import React from 'react';

import './toolbarButton.scss';

interface IToolBarButtonProps {
  title: string;
  onClick ?: () => void;
  image: string;
  disabled ?: boolean;
}

const ToolBarButton = ({ title, onClick, image, disabled }: IToolBarButtonProps) => {
  const className = 'ABBYY_Capture_Module__ToolBar_button ' + (disabled ? 'ABBYY_Capture_Module__ToolBar_button--disabled': '');
  const imgClassName: string = `ABBYY_Capture_Module__ToolBar_button_img ABBYY_Capture_Module__ToolBar_button_img--${image}`;

  return (
    <div className={className} onClick={onClick}>
      <div className={imgClassName} />
      <span className="ABBYY_Capture_Module__ToolBar_button_text">{title}</span>
    </div>
  );
};

export default ToolBarButton;
