import { appSettings } from 'src/services/settings';
import { STRINGS } from 'src/services/localization/strings';
import isIos from 'src/utils/isIos';
import MobileDetect from 'mobile-detect';

const MIN_CAMERA_RESOLUTION = 1000; // by lowest size;

interface ICameraService {
  startStreamToVideo: (videoEl: HTMLVideoElement) => Promise<MediaStream>;
  stopStream: () => void;
}

// current stream we are working with
let currentStream: MediaStream = null;

/** check that stream from device fulfill minimum requirements */
const checkStream = (stream: MediaStream): Promise<MediaStream> => {
  return new Promise((resolve, reject) => {
    const minRes = Math.min(
      stream.getVideoTracks()[0].getSettings().width,
      stream.getVideoTracks()[0].getSettings().height
    );

    if (minRes < MIN_CAMERA_RESOLUTION) {
      stream.getTracks().forEach((track) => track.stop());

      const errorMessage = isIos ? STRINGS.camera_low_res_ios : STRINGS.camera_low_res;

      reject(errorMessage);
      return;
    }

    resolve(stream);
  });
};

const awaitForVideo = (stream: MediaStream, videoEl: HTMLVideoElement) => {
  return new Promise((resolve, reject) => {
    const check = () => {
      if (videoEl.videoWidth !== 0) {
        if (!stream.active) {
          reject(STRINGS.camera_error);
        }

        resolve();
      } else {
        requestAnimationFrame(check);
      }
    };

    check();
  });
};

const cameraService: ICameraService = {
  startStreamToVideo: (videoEl) => {
    if (currentStream) {
      cameraService.stopStream();
    }

    return new Promise((resolve, reject) => {
      // turn off through settings
      if (!appSettings.userOptions.enableCamera) {
        reject(STRINGS.camera_disabled);
        return;
      }

      // edge not supported atm
      if (/Edge/.test(navigator.userAgent)) {
        reject(STRINGS.browser_edge_error);
        return;
      }

      // no media API
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        const deviceInfo = new MobileDetect(window.navigator.userAgent);

        if (isIos && deviceInfo.userAgent() !== 'Safari') {
          // iOS & !Safari = no getUserMedia API
          reject(STRINGS.ios_non_safari_error);
        }

        reject(STRINGS.camera_error);
      }

      // all good, take stream and attach to videoEl
      navigator.mediaDevices
        .getUserMedia({
          video: appSettings.cameraConstraints,
        })
        .then(checkStream) // check that stream good enough
        .then((stream) => {
          // store & attach to videoEl
          currentStream = stream;
          videoEl.srcObject = currentStream;
        })
        .then(() => {
          // wait for video to start playing
          return awaitForVideo(currentStream, videoEl);
        })
        .then(resolve)
        .catch((error: Error) => {
          if (typeof error === 'string') {
            reject(error);
          }

          reject(STRINGS.camera_error);
        });
    });
  },
  stopStream: () => {
    if (currentStream) {
      currentStream.getTracks().forEach((track) => {
        track.stop();
      });
    }
  },
};

export default cameraService;
